export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Projects"])},
        "subtitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["My projects makes use of vast variety of latest technology tools."])}
      },
      "ru": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Проекты"])},
        "subtitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["В моих проектах используется широкий спектр новейших технологических инструментов."])}
      }
    }
  })
}
