export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "category": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Select categories"])},
        "search": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Enter title"])},
        "others": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["others"])},
        "nodata": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["no data"])}
      },
      "ru": {
        "category": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Выберите категорию"])},
        "search": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Введите заголовок"])},
        "others": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["других"])},
        "nodata": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["не найдено"])}
      }
    }
  })
}
